import { mapState ,mapGetters,mapActions}from 'vuex';
import { formateDate, pointInsideCircle, isSameDay } from '@/common/util.js';
import { Location ,UserInfo ,Sign} from './index.js'
export default {
	name:"index",
	components:{
		Location ,UserInfo ,Sign
	},
	data() {
		return {
			isCompany: true, //正常在公司打卡（还是外勤打卡）
			isSign: false, //是否打卡
			
			date: formateDate(new Date(), 'Y-M-D'),
			// 公司位置
			covers: [
				{
					id: 0,
					callout: { content: 'XX科技有限公司', color: 'red', display: 'ALWAYS' },
					latitude: 39.90,
					longitude: 116.40,
					iconPath: '../../../static/img/location.png'
				}
			],
			//打卡信息 （模式，经纬度，地址，时间）
			signInfo: { 
				mode: '', 
				latitude: '', 
				longitude: '', 
				address: '', 
				time: '', 
				remarks: '' ,
			}, 
		};
	},
	computed: {
		...mapGetters(["startTime","endTime"])
	},
	methods: {
		
	}
};
